import React, { useState } from "react";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";

//Style
import "./ContactMe-style.scss";
import { sendContact } from "../../Network/ContactMe";
import styled, { keyframes } from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUser, faEnvelope } from "@fortawesome/free-solid-svg-icons";
import { colors } from "../../style";


function ContactMe() {
  const [name, setName] = useState("");
  const [emailId, setEmailId] = useState("");
  const [message, setMessage] = useState("");

  const [isNameInValid, setNameInValid] = useState(false);
  const [isEmailIdInValid, setEmailIdInValid] = useState(false);

  const [isFormSubmited, setSubmited] = useState(false);
  const [isLoading, setLoading] = useState(false);

  const handleEmailChange = (text: string) => {
    if (text !== "" && text.indexOf("@") >= 0) {
      setEmailIdInValid(false);
    } else if (text === "") {
      setEmailIdInValid(false);
    } else {
      setEmailIdInValid(true);
    }
    setEmailId(text);
  };

  const ErrorMsg = styled.p`
    position: absolute;
    color: red;
    width: 100%;
    text-align: start;
  `;

  const validation = (): boolean => {
    setNameInValid(false);
    setEmailIdInValid(false);
    if (name === "") {
      setNameInValid(true);
      return false;
    }
    if (emailId === "" || emailId.indexOf("@") === -1) {
      setEmailIdInValid(true);
      return false;
    }
    return true;
  };

  const handleSubmit = (e: React.MouseEvent) => {
    setLoading(true);
    e.preventDefault();
    if (validation()) {
      sendContact({ name: name, emailId: emailId, message: message })
        .then((res) => {
          console.log('sucess')
          setSubmited(true);
          setLoading(false);
        })
        .catch((err) => {
          console.log(err);
          setLoading(false);
        });
    } else {
      console.log("error");
      setLoading(false);
    }
  };

  const _renderForm = () => (
    <Row>
      <Col lg={12}>
        <div className="contact-title-container">
          <p className="page-title contact-me">Contact Me</p>
          <p className="contact-title">
            Have a question or want to work together
          </p>
        </div>
      </Col>
      <Col lg={6} className="contact-title-container">
        <div
          className="input-container"
          style={{ ...(isNameInValid && { borderColor: "red" }) }}
        >
          <FontAwesomeIcon icon={faUser} color={colors.secondary} style={{ marginRight: 10, marginLeft: 10 }} />
          <Form.Control
            className="input"
            type="text"
            name="name"
            value={name}
            onChange={(e: any) => {
              setName(e.target.value);
            }}
            placeholder="Name"
          />
        </div>
        {isNameInValid && <ErrorMsg>Please enter your name</ErrorMsg>}
      </Col>
      <Col lg={6} className="contact-title-container">
        <div
          className="input-container"
          style={{ ...(isEmailIdInValid && { borderColor: "red" }) }}
        >
          <FontAwesomeIcon icon={faEnvelope} color={colors.secondary} style={{ marginRight: 10, marginLeft: 10 }} />
          <Form.Control
            className="input"
            type="email"
            value={emailId}
            onChange={(e: any) => handleEmailChange(e.target.value)}
            placeholder="Email ID"
          />
        </div>

        {isEmailIdInValid && (
          <ErrorMsg>Please enter valid email address</ErrorMsg>
        )}
      </Col>
      <Col lg={12} className="contact-title-container">
        <div className="input-container text-area">
          <FontAwesomeIcon icon={faEnvelope} color={colors.secondary} style={{ marginTop: 10, marginRight: 10, marginLeft: 10 }} />
          <Form.Control
            as="textarea"
            rows={5}
            className="input"
            value={message}
            onChange={(e: any) => {
              setMessage(e.target.value);
            }}
            placeholder="Type Message"
          />
        </div>
      </Col>
      <Col lg={12} className="contact-title-container">
        <Button id="submit-but" aria-label="Submit" onClick={handleSubmit}>
          Submit
        </Button>
      </Col>
    </Row>
  );

  const _renderThankyouMsg = () => (
    <div className="contact-title-container">
      <img className="thankyouImg" src={require('../../assets/Images/mailbox.svg')} />
      <h1 className="page-title">Message received. Thanks!</h1>
      <p className="resume-heading ">I'll be in touch with you shortly.</p>
    </div>
  );

  const _Loading = () => {

    const Container = styled.div`
      flex-direction:column;
      text-align : center;
    `;

    const slide = keyframes`
      from{right: 0;}
      to{right: -300px;}
    `;
    const LoadingContainer = styled.div`
      width: 200px;
      height: 10px;
      overflow: hidden;
      position: relative;
      background-color: ${colors.primary};
      border-radius: 1000px;
    `;

    const Progress = styled.div`
      right: 0;
      position: absolute;
      width: 600px;
      height: 100%;
      background: ${colors.secondary};
      background-image: -webkit-linear-gradient(45deg, #303451 0%, #303451 30%, #FEC576 30%, #FEC576 70%, #303451 70%);
      background-image: -webkit-moz-gradient(45deg, #303451 0%, #303451 30%, #FEC576 30%, #FEC576 70%, #303451 70%);
      background-image: -webkit-o-gradient(45deg, #303451 0%, #303451 30%, #FEC576 30%, #FEC576 70%, #303451 70%);
      background-image: linear-gradient(45deg, #303451 0%, #303451 30%, #FEC576 30%, #FEC576 70%, #303451 70%);
      background-size: 25px 10px;
      animation: ${slide} 3s linear infinite;
    `;


    return (
      <Container>
        <h1 className="page-title">Sending...</h1>
        <LoadingContainer>
          <Progress />
        </LoadingContainer>
      </Container>
    )
  }

  return (
    <Container
      id="ContactMe"
      fluid={true}
      className="container contact-container"
    >
      <div className="box">
        {!isLoading ? !isFormSubmited ? _renderForm() : _renderThankyouMsg() : _Loading()}
      </div>
    </Container>
  );
}

export default React.memo(ContactMe);
