const NAME_ID: string = "entry.2005620554";
const EMAIL_ID: string = "entry.1045781291";
const MESSAGE_ID: string = "entry.1449021220";

interface SendContactProps {
  name: string;
  emailId: string;
  message: string;
}

export const sendContact = (body: SendContactProps) => {
  let promise = new Promise((resolve, reject) => {
    let formData = new FormData();
    formData.append(NAME_ID, body.name);
    formData.append(EMAIL_ID, body.emailId);
    formData.append(MESSAGE_ID, body.message);
    const proxyurl = "https://cors-anywhere.herokuapp.com/"
    fetch(
      proxyurl + "https://docs.google.com/forms/u/0/d/e/1FAIpQLSf8wGd5d2mPH0qpkqeq2ko-DeMLEWbubNJCjxylCzCDJvBv1g/formResponse",
      {
        method: "POST",
        body: formData,
        headers:{
          "Access-Control-Allow-Origin" : "*",
        }
      }
    )
      .then((res) => {
        resolve(res);
      })
      .catch((err) => {
        reject(err);
      });
  });
  return promise;
};
